<template>
    <div class="page-container">
        <div class="board-title">{{ boardName }}</div>
        <form novalidate @submit.prevent="validate">
            <input type="file" style="display: none;" ref="img" accept="image/*" @change="imgUpload"/>
            <input type="file" style="display: none;" ref="downFile" @change="dfileUpload"/>

            <div class="product-regist">
                <div class="product-form" style="max-width:100%;padding-top:20px;">
                    <ul class="input-ul">
                        <li v-if="(wAuth=='all' && !isLogin) || modifyUserInfoShow" class="flex-box nouser">
                            <div class="left">
                                <div class="input-box" id="userName-required">
                                    <v-text-field v-model="userName"
                                                  :rules="[() => !!userName || '작성자명을 입력하세요']"
                                                  required
                                                  dense
                                                  placeholder="작성자명을 입력하세요"
                                                  outlined
                                                  ref="userName"
                                                  maxLength="20">

                                    </v-text-field>
                                </div>
                            </div>
                            <div class="right">
                                <div class="input-box" id="userPass-required">
                                    <v-text-field v-model="userPass"
                                                  :rules="[() => !!userPass || '비밀번호를 입력하세요']"
                                                  required
                                                  ref="userPass"
                                                  type="password"
                                                  maxLength="10"
                                                  dense
                                                  placeholder="비밀번호를 입력하세요"
                                                  outlined>

                                    </v-text-field>
                                </div>
                            </div>
                        </li>
                        <li class="flex-box">
                            <div class="left select-box-left" v-if="categoryUse=='yes'">
                                <div class="select-box" id="category-required">
                                    <v-select v-model="category"
                                              dense
                                              outlined
                                              :rules="[() => !!category || '카테고리를 선택하세요']"
                                              required
                                              ref="category"
                                              label="카테고리 선택"
                                              style="max-width:500px;"
                                              :items="categoryList">

                                    </v-select>
                                </div>
                            </div>
                            <div class="right">
                                <div class="input-box" id="subject-required">
                                    <v-text-field v-model="subject"
                                                  :rules="[() => !!subject || '제목을 입력하세요']"
                                                  required
                                                  ref="subject"
                                                  maxLength="45"
                                                  dense
                                                  placeholder="제목을 입력하세요"
                                                  outlined>

                                    </v-text-field>

                                </div>
                            </div>


                        </li>
                        <li>
                            <div class="quill-content-box" style="width:100%;padding-bottom:12px;">
                                <quill-editor ref="myQuillEditor" :content="content"
                                              :options="editorOption"
                                              @change="onEditorChange($event)">
                                </quill-editor>

                            </div>

                        </li>
                    </ul>
                </div>
                <ul class="input-add">
                    <li>
                        <div class="label">태그</div>
                        <div class="input-box">
                            <v-text-field v-model="keyword"
                                          placeholder="쉼표(,)를 이용하여 복수등록 가능"
                                          outlined
                                          dense>
                            </v-text-field>
                        </div>
                    </li>
                    <li>
                        <div class="label">파일 첨부</div>
                        <div class="input-box">
                            <div class="add-file-box">
                                <div class="detail-file-box">
                                    <v-chip
                                            v-for="(dfile,index) in dFileList"
                                            class="file-dlist"
                                            close
                                            color="#000"
                                            outlined
                                            dense
                                            @click:close="removeDfile(index)"
                                    >
                                        {{dfile.orgFileName}}
                                    </v-chip>
                                </div>
                                <v-btn class="blackButton" depressed type="button" @click="addDfile()"><span>파일첨부</span>
                                </v-btn>
                            </div>
                        </div>
                    </li>
                    <li class="setting-li">
                        <div class="label">설정</div>
                        <div class="input-box check-box" style="display:flex;margin-top:-10px;">
                            <v-checkbox color="#000" v-model="replyUse"
                                        value="yes" style="margin-right:20px" label="댓글 허용"></v-checkbox>
                            <v-checkbox v-model="userNotice" color="#000" v-if="isLogin"
                                        value="yes" style="margin-right:20px" label="알림"></v-checkbox>
                            <v-checkbox v-model="open" color="#000"
                                        value="yes" style="margin-right:20px" label="공개"></v-checkbox>
                            <v-checkbox v-if="memberInfo.role == 'ROLE_ADMIN'" v-model="notice" color="#000"
                                        value="yes" label="공지"></v-checkbox>
                        </div>
                    </li>
                </ul>
            </div>
            <div class="button-row">
                <v-btn style="width:250px;margin:auto;margin-right:20px;" class="whiteButton" v-if="isLogin"
                       @click="tempSave()" depressed rounded type="button"><span>임시저장</span></v-btn>
                <v-btn style="width:250px;margin:auto;" class="blackButton" depressed rounded type="submit">
                    <span>게시글 저장</span>
                </v-btn>
            </div>
        </form>

    </div>
</template>


<script>
    import apiService from '@/config/web/apiService'
    import {
        API_URL_GET_ARTICLE_REGIST,
        API_URL_INSERT_ARTICLE,
        API_URL_UPDATE_ARTICLE,
        API_URL_INSERT_ARTICLE_TEMP_IMAGE,
        API_URL_INSERT_ARTICLE_TEMP_FILE,
        API_URL_TEMP_BOARD_IS_ARTICLE,
        API_URL_TEMP_BOARD_ARTICLE
    } from '@/config/web/urls'

    import 'quill/dist/quill.core.css'
    import 'quill/dist/quill.snow.css'
    import 'quill/dist/quill.bubble.css'
    import {quillEditor} from 'vue-quill-editor'
    import JQ from 'jquery'
    import UTIL from '@/common/util'
    import AUTH from "@/config/web/auth";
    import {API_ADMIN_TEMP_BOARD_IS_ARTICLE} from "@/config/admin/urls";

    export default {
        components: {
            quillEditor,
        },
        computed: {
            form() {
                return {
                    userName: this.userName,
                    userPass: this.userPass,
                    category: this.category,
                    subject: this.subject,
                }
            },
            editor() {
                return this.$refs.myQuillEditor.quill
            }
        },
        data() {
            return {
                bid: '',
                id: '',
                info: {},
                boardInfo: {},
                boardList: [],
                bidList: [],
                subject: '',
                content: '',
                editorOption: {
                    placeholder: '내용을 입력하세요',
                    theme: 'snow',
                    modules: {
                        toolbar: {
                            container: [
                                [{'header': 1}, {'header': 2}],               // custom button values
                                ['bold', 'italic', 'underline', 'strike'],        // toggled buttons
                                ['blockquote', 'code-block'],
                                [{'list': 'ordered'}, {'list': 'bullet'}],
                                [{'script': 'sub'}, {'script': 'super'}],      // superscript/subscript
                                [{'indent': '-1'}, {'indent': '+1'}],          // outdent/indent
                                [{'direction': 'rtl'}],                         // text direction
                                [{'size': ['small', false, 'large', 'huge']}],  // custom dropdown
                                [{'header': [1, 2, 3, 4, 5, 6, false]}],
                                [{'color': []}, {'background': []}],          // dropdown with defaults from theme
                                //[{ 'font': [] }],
                                [{'align': []}],
                                ['image'],
                                ['link'],
                                ['clean']
                            ],
                            handlers: {
                                image: this.editorImageHandler,
                            }
                        }
                    }

                },
                btype: '',
                category: '',
                categoryList: [],
                categoryUse: 'no',
                secretUse: 'no',
                secret: '',
                bidDisabled: false,
                imgList: [],
                imgIndex: 0,
                imgSize: {
                    width: 200,
                    height: 200,
                    text: '200×200'
                },
                formHasErrors: false,
                serverFlag: false,
                boardName: '',
                memberInfo: {},
                isLogin: false,
                wAuth: 'user',
                userName: '',
                userPass: '',
                uploadImgType: 'list',
                keyword: '',
                dFileList: [],
                replyUse: 'yes',
                open: 'yes',
                userNotice: 'no',
                sessPass: '',
                notice: 'no',
                modifyUserInfoShow: false,
            };
        },
        created() {

            this.bid = this.$route.params.bid;
            if (this.$route.params.id != 'regist') {
                this.id = this.$route.params.id;
            }
            const member = AUTH.getSession();
            if (member && member?.memberInfo) {
                this.memberInfo = member.memberInfo;
                this.isLogin = true;
            }
            this.getData();
        },
        methods: {
            getData() {
                const frm = {bid: this.bid, id: this.id};
                apiService.post(API_URL_GET_ARTICLE_REGIST, frm, (res) => {
                    if (res.status == 'success') {
                        this.boardInfo = res.board;
                        if (this.boardInfo.wauth == 'admin') {
                            if (this.memberInfo.role != 'ROLE_ADMIN') {
                                this.$router.push('/');
                                return;
                            }
                        }
                        if (this.boardInfo.wauth == 'user') {
                            if (!this.isLogin) {
                                this.$router.back();
                                return;
                            }
                        }
                        this.wAuth = this.boardInfo.wauth;
                        this.boardName = this.boardInfo.bname;

                        this.categoryUse = this.boardInfo.categoryUse;
                        if (this.categoryUse == 'yes') {
                            for (let item of JSON.parse(this.boardInfo.categoryList)) {
                                if (item.use == 'yes') {
                                    this.categoryList.push({text: item.name, value: item.code})
                                }
                            }
                        }
                        if (res.info?.bid) {
                            this.info = res.info;
                            if (this.memberInfo.role != 'ROLE_ADMIN') {
                                if (this.info.userId && this.info.userId != this.memberInfo.uid) {
                                    this.$router.push('/');
                                    return;
                                } else if (!this.info.userId) {
                                    if (sessionStorage.getItem('articleSessPass')) {
                                        this.sessPass = sessionStorage.getItem('articleSessPass');
                                    } else {
                                        this.$router.push('/');
                                        return;
                                    }
                                }
                            }
                        }

                        if (res.tempArticle) {
                            let params = {
                                type:'confirm',
                                message: '임시저장된 게시글이 존재합니다<br>저장된 글을 불러오겠습니까?',
                                doAction:()=>{
                                    this.info = res.tempArticle;
                                    this.setTempArticle();
                                },
                                cancleAction:() => {
                                    if (this.id) {
                                        this.setIsArticle();
                                    }
                                }
                            }
                            this.$eventBus.$emit('modalOpen', params);
                            return;
                        } else {
                            if (this.id) {
                                this.setIsArticle();
                            }
                        }

                    }

                })
            },
            setTempArticle() {
                this.modifyUserInfoShow = false;
                this.setBaseInfo();
            },
            setIsArticle() {
                if (!this.info.userId) {
                    this.modifyUserInfoShow = true;
                    this.userName = this.info.userName;
                    this.userPass = this.info.userPassword;
                } else {
                    this.modifyUserInfoShow = false;
                }
                this.setBaseInfo();
            },
            setBaseInfo() {
                this.subject = this.info.subject;
                this.content = this.info.content;
                this.replyUse = this.info.replyUse;
                this.userNotice = this.info.userNotice;
                this.notice = this.info.notice;
                this.open = this.info.open;


                if (this.info.keywords) {
                    let keywordList = this.info.keywords.split(',');
                    let keywords = [];
                    for (const key of keywordList) {
                        if (key) {
                            keywords.push(key);
                        }
                    }
                    if (keywords.length > 0) {
                        this.keyword = keywords.join(',');
                    }
                }
                if (this.info.dfileOrgNames) {
                    let fileOrgNames = this.info.dfileOrgNames.split(',');
                    let fileNames = this.info.dfileNames.split(',');
                    let searchDfileNames = this.info.searchDfileNames.split(',');
                    let fIndex = 0;
                    for (const fname of fileOrgNames) {
                        let file = fileNames[fIndex]
                        let searchName = searchDfileNames[fIndex]
                        let fData = {
                            orgFileName: fname,
                            fileName: file,
                            searchName: searchName,
                        }
                        this.dFileList.push(fData);
                        fIndex++;
                    }

                }
                if (this.info.orgImgNames) {
                    let orgImgList = this.info.orgImgNames.split(',');
                    let imgs = this.info.imgs.split(',');
                    let searchImgName = this.info.searchImgNames.split(',');
                    let fIndex = 0;
                    for (const imgName of orgImgList) {
                        let file = imgs[fIndex]
                        let searchName = searchImgName[fIndex]
                        this.imgList.push({
                            orgName: imgName,
                            searchName: searchName,
                            name: file
                        });
                        fIndex++;
                    }
                }
                this.setAddInfo();
            },
            setAddInfo() {
                if (this.boardInfo.categoryUse == 'yes') {
                    this.categoryList = [];
                    for (let item of JSON.parse(this.boardInfo.categoryList)) {
                        if (item.use == 'yes') {
                            this.categoryList.push({text: item.name, value: item.code})
                        }
                    }
                    if (this.categoryList.length > 0) {
                        this.categoryUse = 'yes';
                    }
                    if (this.info.category) this.category = this.info.category
                }


            },
            editorImageHandler() {
                this.uploadImgType = 'contentEditor'
                this.$refs.img.click();
            },
            addDfile() {
                this.$refs.downFile.click();
            },
            removeDfile(index) {
                this.dFileList.splice(index, 1)
            },
            dfileUpload() {
                if (this.dFileList.length >= 10) {
                    let params = {
                        message: '첨부파일은 10개 까지만 등록가능합니다',

                    }
                    this.$eventBus.$emit('modalOpen', params);
                    return;
                }

                const imgObj = this.$refs.downFile.files;
                let img = imgObj.length > 0 ? imgObj[0] : null;

                const frm = new FormData();
                frm.append('dfile', img);
                apiService.postFile(API_URL_INSERT_ARTICLE_TEMP_FILE, frm, (res) => {

                    const searchName = res.orgFileName.replace(res.ext, '');
                    let fData = {
                        orgFileName: res.orgFileName,
                        searchName: searchName,
                        fileName: res.saveFileUrl,
                    }
                    this.dFileList.push(fData);
                    this.$refs.downFile.value = '';
                })

            },
            imgUpload() {
                const imgObj = this.$refs.img.files;
                let img = imgObj.length > 0 ? imgObj[0] : null;
                const frm = new FormData();
                frm.append('image', img);
                apiService.postFile(API_URL_INSERT_ARTICLE_TEMP_IMAGE, frm, (res) => {
                    switch (this.uploadImgType) {
                        case 'contentEditor':
                            const range = this.editor.getSelection();
                            let url = apiService.imgHost + 'api/controller/board/getArticleImage?imgName=' + encodeURIComponent(res.saveFileUrl)
                            this.editor.insertEmbed(range.index, 'image', url);
                            const searchName = res.orgFileName.replace(res.ext, '');
                            this.imgList.push({
                                orgName: res.orgFileName,
                                searchName: searchName,
                                name: res.saveFileUrl
                            });

                            break;
                    }
                })
                this.$refs.img.value = '';

            },
            onEditorChange({quill, html, text}) {
                this.content = html
            },
            validate() {

                this.formHasErrors = false
                let firstErrorId = '';
                Object.keys(this.form).forEach(f => {
                    let check = false;
                    switch (f) {
                        case 'userName':
                        case 'userPass':
                            if (this.wAuth != 'all' || this.isLogin) {
                                check = true;
                            }
                            break;
                        case 'category':
                            if (this.categoryUse != 'yes') {
                                check = true;
                            }
                            break;
                    }
                    if (!check) {
                        if (!this.form[f]) {
                            this.formHasErrors = true
                            if (!firstErrorId) firstErrorId = f;
                        }
                        this.$refs[f].validate(true)
                    }

                })
                if (this.formHasErrors) {
                    UTIL.errorPosition('base', '#' + firstErrorId + '-required', () => {

                    })
                    return;
                }
                if (!this.content) {
                    let params = {
                        message: '내용을 입력하세요',

                    }
                    this.$eventBus.$emit('modalOpen', params);
                    return;
                }
                let frm = this.getSaveParams();
                if (!frm) return;

                if (this.serverFlag) return;
                this.serverFlag = true;
                if (this.info?.bid && this.id) {
                    frm.id = this.id;
                }
                this.$eventBus.$emit('overlay', 'open');
                const url = (this.info?.bid && this.id) ? API_URL_UPDATE_ARTICLE : API_URL_INSERT_ARTICLE;
                apiService.post(url, frm, (res) => {
                    this.serverFlag = false;
                    this.$eventBus.$emit('overlay', 'hide');
                    if (res.status == 'success') {
                        let msg = '저장'
                        if (this.id) {
                            msg = '수정'
                        }
                        let params = {
                            message: '게시글 정보가 ' + msg + ' 되었습니다',
                            doAction: () => {
                                this.$router.back();
                            }
                        }
                        this.$eventBus.$emit('modalOpen', params);

                    }
                })
            },
            getSaveParams() {
                const keywordList = this.keyword.split(',');
                const keywords = [];
                for (const key of keywordList) {
                    if (key) {
                        keywords.push(key.replace(/ /g, ''));
                    }
                }
                if (keywords.length > 10) {
                    let params = {
                        message: '키워드는 10개 까지만 등록가능합니다',

                    }
                    this.$eventBus.$emit('modalOpen', params);
                    return false;
                }
                let keywordString = '';
                if (keywords.length > 0) {
                    keywordString = keywords.join(',');
                }
                const regex = /<img[^>]+src=[\"']?([^>\"']+)[\"']?[^>]*>/g
                let extractUrl = '';
                let inputOrgImgList = [];
                let inputImgList = [];
                let searchImgNames = [];
                while (extractUrl = regex.exec(this.content)) {
                    if (extractUrl[1]) {
                        let imgName = extractUrl[1].replace(apiService.imgHost + 'api/controller/board/getArticleImage?imgName=', '');

                        for (let img of this.imgList) {
                            if (imgName == img.name) {

                                inputOrgImgList.push(img.orgName)
                                inputImgList.push(img.name);
                                searchImgNames.push(img.searchName)
                                //inputImgList.push(img);
                            }
                        }
                    }
                }
                let imgString = '';
                let orgImgString = '';
                let searchImgNameString = '';
                if (inputOrgImgList.length > 0) {
                    imgString = inputImgList.join(',');
                    orgImgString = inputOrgImgList.join(',');
                    searchImgNameString = searchImgNames.join(',');
                }
                let orgDfileNameString = '';
                let dfileNameString = '';
                let searchDfileNameString = '';
                if (this.dFileList.length > 0) {
                    let orgFileNames = [];
                    let fileNames = [];
                    let searchFileNames = [];
                    for (const item of this.dFileList) {
                        orgFileNames.push(item.orgFileName);
                        fileNames.push(item.fileName);
                        searchFileNames.push(item.searchName)
                    }
                    orgDfileNameString = orgFileNames.join(',');
                    dfileNameString = fileNames.join(',');
                    searchDfileNameString = searchFileNames.join(',');
                }

                let sessPass = (this.sessPass) ? this.sessPass.toString() : '';
                let frm = {
                    subject: this.subject,
                    category: this.category,
                    userName: this.userName,
                    userPassword: this.userPass,
                    bid: this.bid,
                    content: this.content,
                    //dFileList: this.dFileList,
                    orgDfileNameString: orgDfileNameString,
                    dfileNameString: dfileNameString,
                    searchDfileNameString: searchDfileNameString,
                    //keywords: keywords,
                    keywords: keywordString,
                    sessPass: sessPass,
                    //imgList:inputImgList,
                    imgString: imgString,
                    orgImgString: orgImgString,
                    searchImgNameString: searchImgNameString,
                    notice: ((this.notice == 'yes') ? 'yes' : 'no'),
                    replyUse: ((this.replyUse == 'yes') ? 'yes' : 'no'),
                    open: ((this.open == 'yes') ? 'yes' : 'no'),
                    userNotice: ((this.userNotice == 'yes') ? 'yes' : 'no')
                }
                return frm;

            },
            tempSave() {

                let frm = this.getSaveParams();
                if (!frm) return;
                if (this.serverFlag) return;
                this.serverFlag = true;
                if (this.id) {
                    frm.id = this.id;
                }
                this.$eventBus.$emit('overlay', 'open');
                const url = (this.id) ? API_URL_TEMP_BOARD_IS_ARTICLE : API_URL_TEMP_BOARD_ARTICLE;
                apiService.post(url, frm, (res) => {
                    this.serverFlag = false;
                    this.$eventBus.$emit('overlay', 'hide');
                    let params = {};
                    if (res.status == 'success') {
                        params = {
                            message: '게시글 정보가 임시저장 되었습니다',
                        }
                        this.$eventBus.$emit('modalOpen', params);

                    } else {
                        params = {
                            message: '임시저장에 실패하였습니다',
                        }

                    }
                    this.$eventBus.$emit('modalOpen', params);

                })
            },
        },
        mounted() {
            JQ('.quill-content-box .ql-editor').css('min-height', '400px');

        }

    }
</script>

<style lang="scss" scoped>
    @import 'src/assets/style/common';
    @import 'src/assets/style/input';

    .page-container {
        width: 100%;
        margin: 0;
        padding: 21px 24px;
        min-height: calc(100vh - 300px);
        background: #fff;

        .board-title {
            font-size: 18px;
            font-weight: bold;
            color: #333333;
            border-bottom: solid 1px #333;
            padding-bottom: 10px;
        }

        .select-box {
            max-height: 50px;
        }


        .input-ul {
            padding: 0;
            margin: 0;

            li {
                &.flex-box {
                    display: flex;
                    justify-content: space-between;

                    &.nouser {
                        .left {
                            min-width: 48%;
                            width: 48%;

                        }

                        .right {
                            width: 48%;
                        }
                    }

                    .left {
                        min-width: 50%;
                        display: flex;

                        &.select-box-left {
                            min-width: 200px;
                            margin-right: 10px;
                        }
                    }

                    .right {
                        width: 100%;
                        display: flex;
                    }

                    .label {
                        padding: 10px 10px 0 0;
                        font-weight: 600;
                        font-size: 14px;
                        color: #000;
                    }

                }


                .input-box {
                    width: 100%;
                    position: relative;

                    .error-message {
                        position: absolute;
                        bottom: 8px;
                        left: 10px;
                        color: #880E4F;
                        font-size: 12px;
                    }
                }
            }
        }

        .input-add {
            margin-top: 10px;
            padding: 0;

            li {
                min-height: 60px;
                line-height: 60px;
                display: flex;

                .label {
                    font-size: 14px;
                    font-weight: bold;
                    color: #333;
                    min-width: 80px;
                }

                .add-file-box {
                    display: flex;
                    width: 100%;

                    .detail-file-box {
                        width: 98%;
                        min-height: 43px;
                        height: auto;
                        border: solid 1px #999;
                        padding: 0 0 5px 5px;
